<template>
    <div class="appBox">
        <div>
            <div class="online ov"><span class="groupName">{{groupInfo.name}}</span><router-link :to="{path:'/reddetail', query: {groupId:this.redgroup_id}}" class="fr"><i class="fl">·</i><i class="fl">·</i><i class="fl">·</i></router-link></div>
            <ul class="chat">
                <li v-for="(item,index) in redgroupLog" class="ov">
                    <p class="headImg fl" @click="goUserPage(item.uid)"><img :src="item.userInfo.avatar" alt=""></p>
                    <!--  已领取状态  -->
                    <p v-if="item.islog" @click="openRedInfo(item, index)" class="already fl pr"><img src="@/static/images/hbgroup/image_hongbao_click.png" alt=""><span>恭喜发财，大吉大利</span><a href="javascript:void(0);">{{groupInfo.money}}{{groupInfo.currency_name}}  {{groupInfo.count}}包</a><i v-if="groupInfo.send_rule == 1">抢到最小的发</i><i v-else>抢到最大的发</i></p>
                    <p v-else @click="openRedInfo(item, index)" class="hbImg  fl pr"><img src="@/static/images/hbgroup/image_hongbao_normal.png" alt=""><span>恭喜发财，大吉大利</span><a href="javascript:void(0);">{{groupInfo.money}}{{groupInfo.currency_name}}  {{item.count}}包</a><i v-if="groupInfo.send_rule == 1">抢到最小的发</i><i v-else>抢到最大的发</i></p>
                    <div class="art">
                        <template v-for="tag in item.redtag">
                            <span v-if="tag.type == 'log'">{{tag.text}}</span>
                            <p v-else>{{tag.text}}</p>
                        </template>
                        <p>每隔几秒刷新一下本界面, 以免错过最新红包</p>
                    </div>
                </li>
            </ul>
            <!--<div class="art">-->
                <!--<span>您抢了xxx个xx币</span>-->
                <!--<p>红包已抢完，耗时XX秒</p>-->
                <!--<p>下一个红包将由xx发出</p>-->
            <!--</div>-->
            <!--  公共组件  -->
            <ul class="navigation">
                <div><a href="javascript:void(0);"><img :src="avatar" alt=""><p></p></a></div>
                <li class="sc" :class="{active:groupInfo.collection}"><a @click="collection" href="javascript:void(0);"><i></i><p>{{groupInfo.collection_count}}</p></a></li>
                <li class="pl" @click="showComment"><a href="javascript:void(0);"><i></i><p>{{groupInfo.comment_count}}</p></a></li>
                <li class="zf"><router-link :to="{path:'/redshare', query:{groupId:this.redgroup_id}}"><i></i><p></p></router-link></li>
                <li class="xyg"><a @click="getNext" href="javascript:void(0);"><i></i><p>下一个</p></a></li>
            </ul>


        </div>

        <!--  点击红包展示  -->
        <div class="hbBox" v-show="showRedinfo">
            <!--  红包未被抢完的情况下  -->
            <transition
                    name="custom-classes-transition"
                    enter-active-class="animated zoomIn"
                    leave-active-class="animated zoomOut"
            >
               <a href="javascript:void (0)" v-show="luck" class="money">
                   <div class="moneyTitle"><img :src="redpacket.userInfo.avatar" alt=""><span>{{redpacket.userInfo.nickname}}</span></div>
                   <div class="moneyArt">恭喜发财，大吉大利</div>
                   <button class="grabred" @click="grabRed" :disabled="grabdisabled"></button>
                </a>
            </transition>
            <transition
                    name="custom-classes-transition"
                    enter-active-class="animated zoomIn"
            >
            <!--  红包已被抢空的情况下  -->
                <a href="javascript:void (0)" v-show="noluck" class="nullMoney">
                    <div class="moneyTitle"><img :src="redpacket.userInfo.avatar" alt=""><span>{{redpacket.userInfo.nickname}}</span></div>
                    <div class="moneyArt">手慢了，红包派完了</div>
                    <div class="lookDetail"><span @click="goRedpacket">查看领取详情</span><i></i></div>
                </a>
            </transition>
            <p @click="closeRedInfo" class="closeBtn"><img src="@/static/images/hbgroup/btn_close_yellow.png" alt=""></p>

        </div>

        <transition
                name="custom-classes-transition"
                enter-active-class="animated slideInUp"
                leave-active-class="animated slideOutDown"
        >
            <!--  评论公共组件  -->
            <div v-show="showcomment" class="commentBox">
                <div class="title"><p>{{groupInfo.comment_count}}条评论</p><i class="fr" @click="showcomment = !showcomment"></i></div>
                <div class="commentList">
                    <ul>
                        <van-list
                                v-model="loading"
                                :finished="finished"
                                finished-text="没有更多了"
                                @load="getComment"
                        >
                            <li v-if="comment.length > 0" v-for="item in comment">
                                <img :src="item.userInfo.avatar" alt="">
                                <p>
                                    <span>{{item.userInfo.nickname}}</span>
                                    <span>{{item.content}}</span>
                                    <span>{{item.create_time}}</span>
                                </p>
                            </li>
                        </van-list>
                    </ul>

                </div>
                <div class="inputBox">
                    <input v-model="content" type="text" placeholder="请输入您的精彩评论吧" style="width: 80%">
                    <button @click="sendComment">发送</button>
                </div>
            </div>
        </transition>

        <van-dialog
                v-model="need"
                title="请输入密码"
                :beforeClose="beforeClose"
        >
            <van-field
                    v-model="room_password"
                    input-align="center"
                    type="password"
                    placeholder="请输入密码"

            />
        </van-dialog>
    </div>


</template>

<script>
    import { neePassword, redGroupInfo, online, redgroupLog, collection, getComment, sendComment, next, grabBefore, grabRedpacket} from '@/api/red'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { List } from "vant";
    import 'vant/lib/list/style';
    import Dialog from 'vant/lib/dialog';
    import 'vant/lib/dialog/style';
    import { Field } from 'vant';
    import 'vant/lib/field/style';
    import { mapGetters } from 'vuex'
    import md5 from '@/utils/md5'
    export default {
        name: "Redchat",
        components: {
            [List.name]: List,
            [Dialog.Component.name]: Dialog.Component,
            [Field.name]: Field,
        },
        computed: {
            ...mapGetters([
                'avatar',
                'name',
                'token'
            ])
        },
        data()
        {
            return {
                grabdisabled:false,
                // 房间密码
                room_password:'',
                cpassword:'',
                need:false,
                showBox:1,
                //评论显示
                showcomment:false,
                redgroup_id:'',
                type:'',
                order:'',
                fuid:'',
                currency:'',
                // 群信息
                groupInfo:{},
                comment:[],
                loading:false,
                finished:false,
                page:1,
                content:'',
                nextState:false ,
                redgroupLog:[],
                // 显示抢红包
                showRedinfo:false,
                // 没有红包了
                noluck:false,
                // 有红包啊
                luck:false,
                redpacket:{
                    userInfo:{
                        nickname:'',
                        avatar:''
                    }
                },

            }
        },
        watch:
        {
            redgroup_id(val)
            {
               this.needPass()
            }
        },
        created()
        {


        },
        destroyed()
        {


        },
        mounted()
        {
            this.redgroup_id = this.$route.query.groupId
            this.fuid = this.$route.query.fuid ? this.$route.query.fuid : ''
            this.order = this.$route.query.order ? this.$route.query.order : ''
            this.type = this.$route.query.type ? this.$route.query.type : ''
            this.currency = this.$route.query.currency ? this.$route.query.currency : ''
        },

        methods:{

            initData()
            {

                //初始化
                this.room_password = ''
                this.page = 1
                this.comment = []
                this.redgroupLog = []
                this.getGroupInfo()
                this.getRedgroupLog()
                this.getComment()
            },

            // 密码验证
            needPass()
            {
                neePassword({redgroup_id:this.redgroup_id}).then(res => {

                    if (res.need)
                    {
                        this.need = true
                        this.cpassword = res.password
                    } else
                    {
                        this.initData()
                    }
                })
            },
            // 验证密码
            confirmPass()
            {


            },
            // 验证密码
            beforeClose(action, done)
            {
                if (this.room_password == '' || this.room_password.length == 0)
                {
                    Toast('请输入房间密码')
                    done(false)
                }

                if (md5(this.room_password) == this.cpassword)
                {
                    this.initData()
                    done()

                } else
                {
                    Toast('密码错误,请重新输入')
                    done(false)
                }

            },
            // 获取群信息
            getGroupInfo()
            {
                redGroupInfo({redgroup_id:this.redgroup_id}).then(res => {

                    this.groupInfo = res
                })
            },
            // 红包发送记录
            getRedgroupLog()
            {
                redgroupLog({redgroup_id:this.redgroup_id}).then(res => {

                    this.redgroupLog = this.redgroupLog.concat(res)
                })
            },
            showComment()
            {
                this.showcomment = true
            },
            // 获取评论信息
            getComment()
            {
                this.loading = true
                getComment({page:this.page, redgroup_id:this.redgroup_id}).then(res => {
                    this.loading = false
                    this.page++
                    if (res.length < 20)
                    {
                        this.finished = true
                    }
                    this.comment = this.comment.concat(res)
                })
            },
            // 发送评论
            sendComment()
            {

                if (this.content == '' && this.content.length == 0)
                {
                    Toast('请输入评论内容')
                    return
                }
                sendComment({redgroup_id:this.redgroup_id, content:this.content}).then(res => {

                    Toast('评论成功')
                    this.content = ''
                    this.comment.push(res)
                    this.groupInfo.comment_count++
                })
            },

            async changeOnline(type)
            {
                let res = await online({redgroup_id:this.redgroup_id, type:type})
                this.getGroupInfo()
            },
            // 收藏
            collection()
            {
                if (this.groupInfo.is_create)
                {
                    Toast('创始人不能取消')
                    return
                }
                collection({redgroup_id:this.redgroup_id}).then(res => {

                    this.groupInfo.collection = res.state
                })
            },

            // 下一个
            getNext()
            {
                if (this.nextState)
                {
                    Toast('已经是最后一个了')
                    return
                }
                next({type:this.type, currency:this.currency, page:this.order, fuid:this.fuid}).then(res => {

                    this.nextState = res.none
                    if (res.none)
                    {

                        Toast('已经是最后一个了')
                        return
                    }
                    this.redgroup_id = res.redgroup_id
                    this.order = this.order ?　Number(this.order) + 1 : ''
                    this.$router.replace({path:'/redchat', query:{groupId:res.redgroup_id, order:this.order, type:this.type, currency:this.currency, fuid:this.fuid}})

                })
            },

            // 打开抢红包信息
            openRedInfo(row, index)
            {
                Toast.loading({
                    duration: 0,       // 持续展示 toast
                    forbidClick: true, // 禁用背景点击
                    loadingType: 'spinner',
                });
                this.redpacket = row
                this.grabBefore(row, index)
            },
            // 关闭抢红包信息
            closeRedInfo()
            {
                this.showRedinfo = false
                this.noluck = false
                this.luck = false
            },
            // 抢红包前获取红包信息
            grabBefore(row, index)
            {
                grabBefore({redpacket_id:row.id}).then(res => {

                    setTimeout(() => {
                        Toast.clear()
                        if (res.log)
                        {
                            this.redgroupLog[index].islog = true
                            // 抢过了直接跳转走掉
                            this.$router.push('redpacketlog?redpacketId='+row.id)
                        }
                        else if (res.has)
                        {
                            // 还有可以抢的
                            this.showRedinfo = true
                            this.luck = true
                            this.noluck = false

                        }else
                        {
                            this.redgroupLog[index].islog = true
                            // 来晚了没有了
                            this.showRedinfo = true
                            this.noluck = true
                            this.luck = false
                        }
                    }, 0.5 * 1000)

                })
            },
            // 直接去红包详情吧
            goRedpacket()
            {
                // 抢过了直接跳转走掉
                this.$router.push('redpacketlog?redpacketId='+this.redpacket.id)
            },
            // 抢红包
            grabRed()
            {

                this.grabdisabled = true
                Toast.loading({
                    duration: 0,       // 持续展示 toast
                    forbidClick: true, // 禁用背景点击
                    loadingType: 'spinner',
                })
                grabRedpacket({redpacket_id:this.redpacket.id}).then(res => {

                    this.grabdisabled = false
                    setTimeout(() => {
                        Toast.clear()
                        if (!res.has)
                        {
                            // 没有了
                            Toast('手速不够啥都没抢到啊');

                        } else
                        {
                            if (res.islog)
                            {
                                //抢过了直接飞走
                                this.goRedpacket()
                                return
                            }
                            if (!res.money)
                            {
                                //余额不足
                                Dialog.alert({
                                    title: '系统提醒',
                                    message: '您的余额押金不足, 请先充值。\n' +
                                        '余额押金: 假设您要抢一个红包总金额为10U的红包，您的钱包内需要有10u才可以抢此红包，您钱包内的这10u被称作为余额押金。\n' +
                                        '注：冻结、提现状态下的资金不算做余额押金。'
                                }).then(() => {

                                })

                                return
                            }
                            if (res.success)
                            {
                                // 抢到了飞走
                                this.goRedpacket()
                                return
                            }
                        }
                    }, 0.5 * 1000)
                })
            },
            goUserPage(uid)
            {
                this.$router.push('usergroup?uid='+uid)
            }

        }
    }
</script>

<style scoped>
    @import "../../static/css/redchat.css";
    .appBox .commentBox .inputBox button{
        color: #6f6f6f;
        font-size: 0.14rem;
        padding: 0.02rem 0.06rem;
        float: right;
        border-radius: .03rem;
    }

</style>



